* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

:root {
  --color-primary: rgb(6, 109, 249);
  --color-secondary: rgb(249, 146, 6);
  --color-cta-secondary: rgb(128, 128, 128);
  --color-cta-secondary-hover: rgb(112, 112, 112);
}
::-webkit-scrollbar {
  height: 8px;
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--color-cta-secondary);
  border-radius: 4px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--color-cta-secondary-hover);
}
