.project {
  margin: 0.5rem;
  display: inline-block;
  border-radius: 4px;
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.5);
  width: 16rem;
}
.project img {
  border-radius: 4px 4px 0 0;
  height: 12rem;
  width: 16rem;
  object-fit: cover;
}
.project .project-info {
  padding: 1rem;
}
.project .project-info p {
  overflow-wrap: break-word;
  white-space: normal;
}
.project .project-cta button {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-weight: 700;
}
.project .project-cta button:focus {
  outline: none;
}
.project .project-cta .project-live {
  color: white;
  background-color: var(--color-cta-secondary);
  border: 2px solid var(--color-cta-secondary);
  transition: all ease 0.3s;
  border-radius: 4px;
}
.project .project-cta .project-live:hover {
  background-color: var(--color-cta-secondary-hover);
  border: 2px solid var(--color-cta-secondary-hover);
}
.project .project-cta .project-code {
  color: var(--color-cta-secondary);
  border: none;
  background-color: rgba(0, 0, 0, 0);
  transition: all ease 0.3s;
}
.project .project-cta .project-code:hover {
  color: var(--color-cta-secondary-hover);
}
