.portfolio-home .home-hero {
  height: 24rem;
  /* background: url("../../../assets/hero.jpg");
  background-size: cover;
  background-position: center; */
  background-color: var(--color-cta-secondary);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 12vw;
}
.portfolio-home .home-hero h1 {
  margin-top: 4rem;
  color: white;
  font-size: 2.5rem;
}
.portfolio-home .home-hero h3 {
  color: rgb(36, 36, 36);
  font-size: 2rem;
}

.portfolio-home .home-highlights {
  margin-top: 1rem;
  padding: 0 12vw;
  height: 24rem;
}

.portfolio-home .home-highlights .highlights-control {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.portfolio-home .home-highlights .highlights-control h3 {
  font-size: 1.5rem;
}
.portfolio-home .home-highlights .highlights-control h5 {
  font-size: 1rem;
  cursor: pointer;
  color: var(--color-cta-secondary);
  transition: all ease 0.3s;
  border-bottom: 2px solid rgba(0, 0, 0, 0);
}
.portfolio-home .home-highlights .highlights-control h5:hover {
  border-bottom: 2px solid var(--color-cta-secondary-hover);
}
.portfolio-home .home-highlights .highlights-carousel {
  position: relative;
  width: auto;
  overflow-x: scroll;
  white-space: nowrap;
  padding: 0.5rem 0;
}
@media (max-width: 600px) {
  .portfolio-home .home-hero {
    padding: 0 4vw;
  }
  .portfolio-home .home-highlights {
    padding: 0 4vw;
  }
}
