.nav-responsive {
  list-style: none;
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 4rem;
  right: 0;
  bottom: 0;
  width: 100vw;
  background-color: white;
  border-top: 2px solid var(--color-cta-secondary);
  justify-content: space-around;
  transform: translateX(100%);
  transition: 0.5s ease-in;
  align-items: center;
}
.nav-responsive li {
  margin-left: 1rem;
  color: var(--color-cta-secondary);
  cursor: pointer;
}
.nav-responsive.nav-responsive-open {
  transform: translateX(0%);
  /* animation: navDrawerAnimation 1s ease-in; */
}
@keyframes navDrawerAnimation {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}
